import uOrdinaryList from '../../../components/page-component/u-goods-list/u-ordinary-list.vue';

import { mapGetters, mapState } from "vuex";

export default {
    data() {
        return {
    		card: {
                image_type_info_detail: {
                    goods: [],
                    cats: [],
                    all: false
                }
            },
            is_free_delivery: 0,
            page: 1,
            more: false,
            id: 0
        }
    },
    components: {
        uOrdinaryList
    },
    computed: {
        ...mapState({
            svipImg: state => state.mallConfig.__wxapp_img.vip_card
        }),
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        })
    },
    methods: {
        toCat(id) {
            uni.navigateTo({
                url: '/pages/goods/list?cat_id=' + id
            });
        },

        getCard(id) {
            let that = this;
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.$request({
                url: that.$api.vip_card.index_right
            }).then(response=>{
                if(response.code === 0) {
                    that.id = id;
                    that.card = response.data.right;
                    that.is_free_delivery = response.data.right.is_free_delivery;
                    if(that.card.image_type_info_detail.all) {
                        that.getRecommend();
                    }else {
                        that.$hideLoading();
                    }
                }else {
                    that.$hideLoading();
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.$hideLoading();
            });
        },
        getList() {
            let that = this;
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.$request({
                url: that.$api.vip_card.index,
            }).then(response=>{
                if(response.code === 0) {
                    that.card.image_type_info_detail.cats = response.data.user.image_type_info_detail.cats;
                    that.card.image_type_info_detail.goods = response.data.user.image_type_info_detail.goods;
                    that.card.image_type_info_detail.all = response.data.user.image_type_info_detail.all;
                    that.is_free_delivery = response.data.user.image_is_free_delivery;
                    if(that.card.image_type_info_detail.all) {
                        that.getRecommend();
                    }else {
                        that.getGoods();
                    }
                }else {
                    that.$hideLoading();
                    if(response.msg === '超级会员卡已关闭' || response.msg === '无超级会员卡权限') {
                        uni.redirectTo({
                            url: '/pages/index/index'
                        })
                    }
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.$hideLoading();
            });
        },
        getRecommend() {
            let that = this;
            that.$request({
                url: that.$api.vip_card.recommend,
            }).then(response => {
                that.$hideLoading();
                if(response.code === 0) {
                    that.card.image_type_info_detail.goods = response.data.list;
                }else {
                    if(response.msg === '超级会员卡已关闭' || response.msg === '无超级会员卡权限') {
                        uni.redirectTo({
                            url: '/pages/index/index'
                        })
                    }
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.$hideLoading();
            });
        },
        getGoods() {
            let that = this;
            that.more = false;
            that.$request({
                url: that.$api.vip_card.right,
                data: {
                    page: that.page
                }
            }).then(response=>{
                that.$hideLoading();
                that.first =  false;
                if(response.code === 0) {
                    that.card.image_type_info_detail.goods = response.data.list;
                    that.page++;
                    if(response.data.list.length == response.data.pagination.pageSize) {
                        that.more = true;
                    }
                }else {
                    if(response.msg === '超级会员卡已关闭' || response.msg === '无超级会员卡权限') {
                        uni.redirectTo({
                            url: '/pages/index/index'
                        })
                    }
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.$hideLoading();
            });
        }
    },
    onReachBottom() {
        if(this.id == 0 && this.more) {
            this.getGoods();
        }
    },
    onLoad(options) { this.$commonLoad.onload(options);
        if(options.id > 0) {
            this.getCard(options.id);
        }else {
            this.getList();
        }
    }
}
